<template>
  <div>
    <headerComponent
      :title="title"
      :subTitle="subTitle"
      :isLoading="isLoading"
      :pageURL="pageURL"
    ></headerComponent>

    <div class="mdui-container" style="max-width: 600px">
      <br /><br />
      <div class="mdui-card mdui-hoverable" style="border-radius: 16px">
        <div class="mdui-card-primary">
          <div class="mdui-card-primary-title">立即表白</div>
          <div class="mdui-card-primary-subtitle">快向你喜欢的TA表白吧！</div>
        </div>
        <div class="mdui-divider"></div>
        <div class="mdui-card-content">
          <div class="mdui-textfield">
            <label class="mdui-textfield-label">你的QQ</label>
            <textarea
              v-model="qq"
              class="mdui-textfield-input"
              placeholder="2333333333"
            ></textarea>
          </div>
          <div class="mdui-textfield">
            <label class="mdui-textfield-label">你的名字</label>
            <textarea
              v-model="name"
              class="mdui-textfield-input"
              placeholder="镜音连"
            ></textarea>
          </div>
          <div class="mdui-textfield">
            <label class="mdui-textfield-label">TA的名字</label>
            <textarea
              v-model="taName"
              class="mdui-textfield-input"
              placeholder="镜音铃"
            ></textarea>
          </div>
          <div class="mdui-textfield">
            <label class="mdui-textfield-label">表白配图（可选）</label>
            <textarea
              v-model="image"
              class="mdui-textfield-input"
              placeholder="https://kagamine.top/img.png"
            ></textarea>
          </div>
          <div class="mdui-textfield">
            <label class="mdui-textfield-label">一句话介绍一下TA</label>
            <textarea
              v-model="introduceTA"
              class="mdui-textfield-input"
              placeholder="镜音铃是一个元气的二次元少女"
            ></textarea>
          </div>
          <div class="mdui-textfield">
            <label class="mdui-textfield-label">你要对TA说的话</label>
            <textarea
              v-model="toTA"
              class="mdui-textfield-input"
              rows="4"
              placeholder="我喜欢你..."
            ></textarea>
          </div>
        </div>

        <div class="mdui-card-actions">
          <button
            :disabled="isdisabledFn"
            class="mdui-btn mdui-color-theme-accent mdui-ripple mdui-float-right"
            v-on:click="submit()"
          >
            发射！
          </button>
        </div>
      </div>
      <br /><br />
    </div>
  </div>
</template>
<script>
import headerComponent from "../components/header";
import { request } from "../utils/request.js";
import md5 from "js-md5";
export default {
  data() {
    return {
      title: "未知表白墙",
      subTitle: "去表白",
      isLoading: false,
      isdisabledFn: false,
      pageURL: "/submit",
      qq: "",
      name: "",
      taName: "",
      image: "",
      introduceTA: "",
      toTA: "",
    };
  },
  components: {
    headerComponent: headerComponent,
  },
  methods: {
    submit() {
      this.isLoading = true;
      this.isdisabledFn = true;
      let param = new URLSearchParams();
      this.timestamp = Date.parse(new Date()) / 1000;
      param.append(
        "key",
        md5(
          this.qq +
            this.name +
            this.taName +
            this.image +
            this.introduceTA +
            this.toTA +
            this.timestamp
        )
      );
      param.append("timestamp", this.timestamp.toString());
      param.append("contact", this.qq);
      param.append("name", this.name);
      param.append("taName", this.taName);
      param.append("image", this.image);
      param.append("introduceTA", this.introduceTA);
      param.append("toTA", this.toTA);
      request(
        this,
        {
          url: `https://api.llilii.cn/loveway/submit.php`,
          method: "post",
          data: param,
        },
        function (that, rdata) {
          that.isdisabledFn = false;
          if (rdata.code == 1) {
            that.qq = "";
            that.name = "";
            that.taName = "";
            that.image = "";
            that.introduceTA = "";
            that.toTA = "";
            mdui.snackbar({
              message: "提交成功！",
              position: "right-top",
            });
          } else {
            mdui.snackbar({
              message: rdata.msg,
              position: "right-top",
            });
          }
        }
      );
    },
  },
};
</script>



